  .btnMobile{
    font-size: 14px;
  }
  .carousel-caption{
    position: absolute; 
    top: 20px; 
    z-index: 10;
    color: #fff;
    text-align: center;
  }
  .carousel-indicators{
    position: absolute; 
    top: 600px;
  }
  .carousel-indicators li {
    border-radius: 50%;
    border: .5px solid #006f9b !important;
    box-shadow: none !important;
    width: 15px !important;
    height: 15px !important;
  }
  .caption-label{
    color: #006f9b;
    font-size: 40px;
    margin: 0 !important;
  }
  .caption-text{
    color: grey;
    font-size: 25px;
    font-weight: lighter;
  }
  .carousel-indicators li {
    display: inline-block;
    width: 60px;
    height: 60px;
    margin: 10px;
    text-indent: 0;
    cursor: pointer;
    border: none;
    box-shadow: inset 1px 1px 1px 1px rgba(0,111,155,0.7);    
  }
  
  .carousel-indicators .active {
    width: 48px;
    height: 48px;
    margin: 10px;
    background-color: #006f9b;
  }
  .carousel-control-prev-icon,
  .carousel-control-next-icon {
    display: hidden;
  }
  
  .carousel-control-next-icon:after
  {
    display: hidden;
  }
  
  .carousel-control-prev-icon:after {
    display: hidden;
  }
  .mobileLogo{
    display: flex;
    margin: 0 auto;
